import { Injectable } from '@angular/core';

@Injectable()
export class CommonService {

    scheduleType: any = {
        TYPE_EDIT_EXISTING: 0,
        TYPE_GENERATE_NEW: 1,
        TYPE_GENERATE_SPECIAL: 2
    };
    
    w_hType: any = {
        TYPE_HOLIDAY: 0,
        TYPE_WEEKLY: 1,
        TYPE_WEEKLY_NEW: 2,
    };
    
    scheduleDisabilty: any = {
        TYPE_ENABLED: 1,
        TYPE_DISABLED: 0,
    };
    
    areaType: any = {
        TYPE_NEWYORK: 1,
        TYPE_BA: 2,
    };

    constructor() { }
    
    public convertMonthFormat(m) {
            let month = new Array();
            month[0] = "JANUARY";
            month[1] = "FEBRUARY";
            month[2] = "MARCH";
            month[3] = "APRIL";
            month[4] = "MAY";
            month[5] = "JUNE";
            month[6] = "JULY";
            month[7] = "AGUEST";
            month[8] = "SEPTEMBER";
            month[9] = "OCTOBER";
            month[10] = "NOVEMBER";
            month[11] = "DECEMBER";
            return month[m];
    }
    
    public convertDayOfWeekFormat(w) {
            let dow = new Array();
            dow[0] = "Sunday";
            dow[1] = "Monday";
            dow[2] = "Tuesday";
            dow[3] = "Wednesday";
            dow[4] = "Thursday";
            dow[5] = "Friday";
            dow[6] = "Saturday";
            return dow[w];
    }
    
    public convertWeekToNumber(w) {
            let dow = new Array();
            dow["Sunday"] = 0;
            dow["Monday"] = 1;
            dow["Tuesday"] = 2;
            dow["Wednesday"] = 3;
            dow["Thursday"] = 4;
            dow["Friday"] = 5;
            dow["Saturday"] = 6;
            return dow[w];
    }

    public getLatestWeeklyDate(param_dateArray) {
            let max = param_dateArray[0];
        
            for (let i=0; i<Object.keys(param_dateArray).length; i++) {
                let item = param_dateArray[i];
                
                let ts = new Date(item).getTime();
                let ts_max = new Date(max).getTime();
                
                if (ts_max < ts) {
                    max = item;
                }
            }
            
            return max;
    }
    
    public getCityName(w) {
            let city = new Array();
            city[1] = "New York";
            city[2] = "BETHESDA/ARLINGTON";
            return city[w];
    }

}



